
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

import Web3 from "web3";
import NftJson from "../abis/NftJson.json";

import { useWeb3Context } from "../hooks/web3";

export default function Header(props) {

    const { connect, disconnect, connected, providerChainID, checkWrongNetwork, address, provider, chainID } = useWeb3Context();

    const [nftContract, setNftContract] = useState(null);
    const [mainLoading, setMainLoading] = useState(false);
    const [totalMineted, setTotalMineted] = useState(null)
    const [numberMint, setNumberMint] = useState(2);
    // const [mintCost, setMintCost] = useState(0.03)
    const [noticeToShow, setNoticeToShow] = useState("Please connect wallet to Mint NFT");


    const incNumber = () =>
        numberMint > 19 ? setNumberMint(numberMint) : setNumberMint(numberMint + 1);
    const decNumber = () =>
        numberMint > 1 ? setNumberMint(numberMint - 1) : setNumberMint(numberMint);

    useEffect(() => {
        if (connected) {
            loadData();
        }
    }, []);

    useEffect(() => {
        if (connected) {
            if (providerChainID == chainID) {
                loadData();
            } else {
                checkWrongNetwork()
            }
        }
    }, [connected]);



    const loadData = async () => {
        setMainLoading(true);
        const web3 = new Web3(provider);
        const networkAddress = '0xE73d37E849817dC8a9D46C159b09E71EFac385e0';
        const _nftContract = await new web3.eth.Contract(NftJson.abi, networkAddress);
        setNftContract(_nftContract)

        // const _publicCost = await _nftContract.methods.PRICE().call();
        // setMintCost(_publicCost.toString() / 10 ** 18)
        setNoticeToShow('Connected, Ready to mint NFT')
        const totalSupply = await _nftContract.methods.totalSupply().call();
        setTotalMineted(totalSupply.toString())
        setMainLoading(false);
    }


    const mintNFT = async () => {
        if (connected) {
            setNoticeToShow('Minting, Please Wait...');
            setMainLoading(true);
            
                const _publicCost = await nftContract.methods.everyMintCost().call();
                const _mintCostNow = _publicCost.toString();

                await nftContract.methods
                    .mint(numberMint)
                    .send({ from: address, value: _mintCostNow * numberMint })
                    .on('confirmation', (confirmationNumber, receipt) => {
                        console.log(receipt);
                        loadData();
                    })
                    .on('error', function (error, receipt) {
                        console.log(error);
                        setNoticeToShow(error.message);
                        loadData();
                    });
            

        }
    }




    return (
        <>
        <div className="header" style={{backgroundColor: 'rgb(215, 227, 255)'}}>
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
              


					<a className="logo"><img src="images/1.png" width="70" height="70" alt="" /> LilFLoki</a>
				
				

				  <div className="h-main">
                      {connected
                      ?
                      <a href="#" onClick={connect} className="wallet-btn">{`${address.substring(0,5)}...${address.substring(address.length - 5,address.length)}`}</a>
                    :
                    <a href="#" onClick={connect} className="wallet-btn">CONNECT WALLET</a>
                    }
                      <div className="social">
                        <ul>
                          <li><a href="https://twitter.com/thelilfloki/"><img src="images/twitter.png" width="35px" height="35px" alt=""/></a></li>
                          <li><a href="https://t.me/thelilfloki"><img src="images/telegram.png" width="29px" height="29px"alt=""/></a></li>
                          {/* <!--li><a href="#"><img src="images/opensea.svg" alt=""></a></li--> */}
                        </ul>
					  </div>
                  </div>
              </div>
            </div>
          </div>
        </div>
        <section className="banner-sec" style={{backgroundColor: 'rgb(215, 227, 255)'}}>
				  <div className="container">
					<div className="row">
					  <div className="col-lg-6">
					    <div className="banner-img">
						  <img src="images/HAND.png" alt=""/>
						</div>
					  </div>
					  <div className="col-lg-6">
						<div className="banner-text">
						  <h1 style={{backgroundColor: '#001aff', textAlign: 'center'}}>Lil Floki
						</h1>
						    <h6 style={{color: 'black'}}>Taking The NFT World By Storm.</h6>
							  <p style={{color: 'black'}}>The worlds first token dedicated to donating to New Zealand dog shelters while also giving BNB rewards for holders*

							  </p>
							  <p style={{color: 'black'}}>The LilFlokis public sales opens on Saturday, January 15th, 8pm EST.</p>
					       <div className="mint-solpup-main">
						     <h3 style={{color: 'black'
							 }}>Buy
								Soon!</h3>
							   <p style={{color: 'black'}}>Buy LilFloki 
							   </p>
                               {totalMineted != null&&
                               <p style={{color: 'black'}}>total Minted Nfts: {totalMineted} 
							   </p>
                               }
							     <div className="min-qty-main">
								   <div className="control">
									 <button  onClick={decNumber} className="bttn bttn-left" id="minus"><i className="fa fa-minus" aria-hidden="true"></i></button>
									   <input  value={numberMint} type="number" className="input" id="input"/>
									 <button onClick={incNumber} className="bttn bttn-right" id="plus"><i className="fa fa-plus" aria-hidden="true"></i></button>
								   </div>
									 <a className="mint-now-btn" aria-setsize="150" href="#">Sale: 15/01/22</a>
									 <br/>
									 <button  disable={connected} className="mint-now-btn-full" onClick={mintNFT}>Mint Nft</button>
                                     <p style={{color:'black'}}>{noticeToShow}</p>
									 <a className="mint-now-btn-full" href="invite/G39ha3V9Xf.html">Join Our Discord!</a>
								   <div className="clear"></div>
								 </div>
						   </div>
						</div>
					  </div>
					</div>
				  </div>
				</section>
                </>
    )
}
